            </p>
            <p class="value-p">
              {{ collaboratorData.principal.roleVOS[0].roleName }}
            </p>
          </div>
        </div>

        <div
          v-if="collaboratorData.leaderUserVOS.length!=0&&$route.query.OTP!=2"
          class="tab4_title"
        >
          <span>上级领导</span>
        </div>
        <div v-if="collaboratorData.leaderUserVOS.length!=0&&$route.query.OTP!=2">
          <div
            v-for="(item) in collaboratorData.leaderUserVOS"          
            :key="item.uid"
            class="tab4-list"
          >
            <div class="tab4-left-box">
              <div class="img-box">
                <img src>
              </div>
            </div>
            <div class="tab4-right-box">
              <p class="title-p">
                {{ item.userNo }}
              </p>
              <p class="value-p">
                {{ item.name }}
              </p>
            </div>
          </div>
        </div>
        <div
          v-if="collaboratorData.collaboratorUserVOS.length!=0&&$route.query.OTP!=2"
          class="tab4_title"
        >
          <span>协作人</span>
        </div>
        <div v-if="collaboratorData.collaboratorUserVOS.length!=0&&$route.query.OTP!=2">
          <div 
            v-for="(item) in collaboratorData.collaboratorUserVOS"                     
            :key="item.uid"            
            class="tab4-list"                      
          >
            <div class="tab4-left-box">
              <div class="img-box">
                <!-- <img src> -->
              </div>
            </div>
            <div class="tab4-right-box">
              <p class="title-p">
                {{ item.userNo }}
              </p>
              <p class="value-p">
                {{ item.name }}
              </p>
            </div>
            <van-checkbox
              v-if="$route.query.type!=2"
              v-model="item.isEmployee"
              class="delete-icon"   
              checked-color="#03C061"
            />
            <van-radio-group
              v-if="$route.query.type==2"
              v-model="radio"
            >   
              <van-radio
                checked-color="#03C061"
                :name="item.id"
                class="delete-icon"
              />  
            </van-radio-group>          
          </div>        
        </div>
      </div>

      <div class="tab-footer">
        <van-button
          v-if="$route.query.OTP!=2"
          class="gradual-btn"
