<script>
// import { api, get, post } from "@/service";
// import { mapState } from 'vuex'
// import { formatDate, isNull } from '@/utils'
import { collaboratorIdInfo,collaboratorUpdate,leading } from '@/service/customer'
export default {
  name: 'AddCooperation',
  data() {
    return {
      checked: true,
      collaboratorData:{},
      radio:''
    }
  },
  computed: {},
  watch: {},
  created() {
    this.getCollaborator()
    
    if(this.$route.query.type==2){

    }else{
      
    }
    
  },
  methods: {
    //协作人list
    getCollaborator(){
      // console.log(this.$store.getters.userData)
      collaboratorIdInfo({
        id:this.$store.getters.userData.companyId
      }).then(res=>{
        this.collaboratorData = res.data
        if(this.$route.query.type==2){

          this.collaboratorData.collaboratorUserVOS.map(d=>{
            d.isEmployee = false
          })
        }
        // console.log('协作人list',res)
      })
    },
    setBut(){
      if(this.$route.query.type==2){
        this.setLeading()
        
      }else{
        this.setCollaborator()
        
      }      
         
    },
    changeLeading(idx){
      console.log(idx)
      console.log(this.radio)
      this.collaboratorData.collaboratorUserVOS.map((d,iddx)=>{
        if(idx!=iddx)d.isEmployee = false
      })
    },
    setLeading(){
      
      let params = {
        leadingOfficialUserId:this.radio
      }
      leading(params).then(res=>{
        this.$router.go(-2)   
      })
    },
    //编辑协作人
    setCollaborator(){
      let arr = []
      this.collaboratorData.collaboratorUserVOS.map(d=>{
        d.isEmployee ? arr.push(d.id):''
      })
      let params = {
        userIds:arr
      }
      collaboratorUpdate(params).then(res=>{
        this.$router.go(-1)   
      })
    },
  },
}
</script>
